body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: #333333;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.gold-text {
  color: #d1a954;
}

.container {
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 60px;
  }
}
@media (min-width: 992px) {
  .container {
    padding: 0 100px;
  }
}
@media (min-width: 1360px) {
  .container {
    padding: 0 200px;
  }
}

.main-header {
  background-image: url("../img/mobile/bg-header.png");
  background-repeat: no-repeat;
  background-size: 768px 530px;
  padding: 40px 25px 80px 25px;
}
@media (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/mobile/bg-header@2x.png");
    background-repeat: no-repeat;
    background-size: 768px 530px;
  }
}
.main-header a {
  text-decoration: none;
  color: #ffffff;
  transition: 0.4s;
}
.main-header a:hover, .main-header a:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.main-header__info-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 310px;
  margin: 0 auto;
  padding-top: 50px;
}

.main-header__main-title {
  font-size: 35px;
  letter-spacing: 4px;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.main-header__main-info {
  width: 310px;
  margin: 0;
}

.main-header__poster-link {
  display: inline-flex;
  font-size: 18px;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 50px;
  background-color: #d1a954;
  margin-top: 50px;
  margin-right: auto;
}

@media (min-width: 768px) {
  .main-header {
    background-image: url("../img/tablet/bg-header.png");
    background-size: 992px 800px;
    padding: 50px 60px 300px 60px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/tablet/bg-header@2x.png");
    background-size: 992px 800px;
  }
}
@media (min-width: 768px) {
  .main-header__info-block {
    max-width: 510px;
    padding-top: 140px;
  }
}
@media (min-width: 768px) {
  .main-header__main-title {
    font-size: 50px;
  }
}
@media (min-width: 768px) {
  .main-header__main-info {
    width: 510px;
  }
}
@media (min-width: 992px) {
  .main-header {
    padding: 50px 100px 300px 100px;
  }
}
.main-nav {
  display: flex;
  position: relative;
}

.main-nav__nav-button {
  display: block;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__nav-button--open {
  width: 40px;
  height: 40px;
  background-image: url("../img/burger-open.svg");
  background-repeat: no-repeat;
}
.main-nav__nav-button--close {
  position: absolute;
  top: 40px;
  left: 160px;
  width: 32px;
  height: 32px;
  background-image: url("../img/burger-close.svg");
  background-repeat: no-repeat;
}

.main-nav__nav-block {
  position: absolute;
  height: 410px;
  top: -40px;
  transform: translateX(-200%);
  background-color: #333333;
  padding: 100px 30px 0 30px;
  z-index: 3;
  transition: 0.4s;
}
.main-nav__nav-block--open {
  transform: translateX(-25px);
  transition: 0.4s;
}

.main-nav__nav-list {
  font-weight: 700;
  text-transform: uppercase;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__nav-item {
  font-size: 16px;
  letter-spacing: 1px;
}
.main-nav__nav-item:not(:last-of-type) {
  margin-bottom: 20px;
}
.main-nav__nav-item a:hover, .main-nav__nav-item a:focus {
  border-bottom: 1px solid #d1a954;
}

.main-nav__link {
  height: 100%;
  border-bottom: 2px solid #d1a954;
  margin-left: auto;
}

@media (min-width: 768px) {
  .main-nav__nav-button--close {
    left: 205px;
  }

  .main-nav__nav-block {
    height: 660px;
    top: -50px;
    padding: 140px 60px 0 60px;
  }
  .main-nav__nav-block--open {
    transform: translateX(-60px);
  }

  .main-nav__nav-item {
    font-size: 18px;
  }
  .main-nav__nav-item:not(:last-of-type) {
    margin-bottom: 30px;
  }
}
.about {
  padding-top: 80px;
}
.about h3 {
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 40px;
}

.about__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.about__our-info {
  text-align: center;
  margin-top: 0;
  margin-bottom: 50px;
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about__info-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.about__info-block h3 {
  flex-basis: 100%;
}
.about__info-block p {
  position: relative;
  margin: 0;
  padding-top: 80px;
}
.about__info-block p:nth-of-type(1)::before {
  content: "";
  position: absolute;
  bottom: 35px;
  left: 0;
  width: 70px;
  height: 70px;
  background-image: url("../img/armchair.svg");
  background-repeat: no-repeat;
}
.about__info-block p:nth-of-type(2)::before {
  content: "";
  position: absolute;
  bottom: 35px;
  left: 0;
  width: 70px;
  height: 70px;
  background-image: url("../img/theater.svg");
  background-repeat: no-repeat;
}

.about__theatre-image {
  display: none;
}

@media (min-width: 768px) {
  .about__info {
    flex-direction: row;
    column-gap: 20px;
  }

  .about__intro-block h3 {
    text-align: left;
  }

  .about__our-info {
    text-align: left;
    width: 445px;
    margin-bottom: 35px;
  }

  .about__info-block {
    flex-direction: column;
  }
  .about__info-block h3 {
    flex-basis: auto;
    text-align: left;
  }
  .about__info-block p:nth-of-type(1) {
    margin-bottom: 15px;
  }
}
.performances {
  padding-top: 80px;
}

.performances__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.performances__main-block {
  height: 350px;
}

.performances__list {
  width: 310px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.performances__item a {
  display: block;
  width: 310px;
  height: 350px;
  transition: 0.4s;
}
.performances__item a:hover, .performances__item a:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.performances__item:nth-of-type(1) a {
  background-image: url("../img/project-1.png");
  background-repeat: no-repeat;
  background-size: 310px 350px;
}

.performances__slider-list {
  display: flex;
  column-gap: 10px;
  width: 70px;
  margin: 10px auto 40px auto;
  padding: 0;
  list-style: none;
}

.performances__slider-button {
  width: 10px;
  height: 10px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: #989898;
  cursor: pointer;
  transition: 0.4s;
}
.performances__slider-button:hover, .performances__slider-button:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.performances__slider-button--active {
  border: 2px solid #ffffff;
}

@media (min-width: 768px) {
  .performances__list {
    display: flex;
    width: 620px;
  }

  .performances__item:nth-of-type(2) a {
    background-image: url("../img/project-2.png");
    background-repeat: no-repeat;
    background-size: 310px 350px;
  }
  .performances__item:nth-of-type(3), .performances__item:nth-of-type(4) {
    display: none;
  }
}
.rating {
  display: flex;
  justify-content: center;
}

@media (min-width: 768px) {
  .rating img {
    width: 768px;
    height: 550px;
  }
}
.partners {
  padding-top: 80px;
}

.partners__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.partners__main-block {
  display: flex;
  justify-content: center;
}

.partners__list {
  display: grid;
  grid-template-columns: min-content min-content;
  gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.partners__item {
  width: 150px;
  height: 70px;
  background-image: url("../img/gazprom_logo.svg");
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .partners__list {
    grid-template-columns: repeat(4, min-content);
    gap: 17px;
  }
}
.donate {
  padding-top: 80px;
}

.donate__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.donate__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.donate__form fieldset {
  display: grid;
  grid-template-columns: 147px 147px;
  grid-template-rows: min-content;
  gap: 10px;
  align-self: center;
  margin: 0;
  padding: 0;
  border: none;
}
.donate__form input {
  width: 135px;
  background-color: #444444;
  color: #ffffff;
  padding: 10px 0 10px 10px;
  border: 1px solid #616161;
  transition: 0.4s;
}
.donate__form input:hover, .donate__form input:focus {
  border: 1px solid #333333;
  transition: 0.4s;
}
.donate__form textarea {
  width: 292px;
  height: 80px;
  background-color: #444444;
  color: #ffffff;
  margin: 0;
  padding: 10px 0 10px 10px;
  border: 1px solid #616161;
  resize: none;
  transition: 0.4s;
}
.donate__form textarea:hover, .donate__form textarea:focus {
  border: 1px solid #333333;
  transition: 0.4s;
}

.donate__name-zone,
.donate__contact-zone {
  display: grid;
  row-gap: 10px;
}

.donate__button {
  font-weight: 700;
  text-transform: uppercase;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 190px;
  height: 50px;
  background-color: #d1a954;
  color: #ffffff;
  margin: 40px 0 0 0;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.donate__button:hover, .donate__button:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.donate__confidentiality {
  text-align: center;
  color: #989898;
  margin-top: 30px;
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .donate__form {
    display: grid;
    grid-template-columns: repeat(3, min-content);
    grid-template-rows: min-content min-content;
  }
  .donate__form fieldset {
    grid-column: 1/4;
    grid-row: 1;
    display: flex;
    column-gap: 20px;
  }
  .donate__form input {
    width: 150px;
  }
  .donate__form textarea {
    width: 272px;
  }

  .donate__button {
    grid-column: 3;
    grid-row: 2;
    margin-left: auto;
  }

  .donate__confidentiality {
    text-align: left;
    grid-column: 1/3;
    grid-row: 2;
  }
}
.main-footer__contact-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
}
.main-footer__contact-block p {
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}
.main-footer__contact-block a {
  text-decoration: none;
  color: #ffffff;
  transition: 0.4s;
}
.main-footer__contact-block a:hover, .main-footer__contact-block a:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.main-footer__adress-block {
  margin-top: 15px;
  text-align: center;
}

.main-footer__copyright-block {
  text-align: center;
  color: #989898;
  border-top: 1px solid #989898;
  margin-top: 30px;
  padding-bottom: 20px;
}
.main-footer__copyright-block p {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .main-footer__contact-block {
    font-size: 16px;
    line-height: 26px;
    flex-direction: row;
    justify-content: space-between;
  }

  .main-footer__adress-block {
    margin-top: 0;
  }

  .main-footer__copyright-block {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
  }
  .main-footer__copyright-block p {
    margin-top: 30px;
  }
}